// Vue
import Vue from "vue";
// Vuex
import Vuex from "vuex";
// Vuex
Vue.use(Vuex);

// 
import video from "./List/video"
import game from "./List/game"
import study from "./List/study"
import app from "./List/app"
import book from "./List/book"
import netdisk from "./List/netdisk"
import script from "./List/script"

const gameContent = {
  namespaced: true, //
  // action 
  actions: {},
  // mutation state
  mutations: {},
  //  state 
  state: {
    // 
    gameList: game,
  },
};
const videoContent = {
  namespaced: true,
  state: {
    // 
    videoList: video
  },
};
const studyContent = {
  namespaced: true,
  state: {
    studyList: study
  },
};
const appContent = {
  namespaced: true,
  state: {
    appList: app
  },
};
const bookContent = {
  namespaced: true,
  state: {
    // 
    bookList: book
  },
};
const netdiskContent = {
  namespaced: true,
  state: {
    // 
    netdiskList: netdisk
  },
};

const scriptContent = {
  namespaced: true,
  state: {
    scriptList: script
  },
};




// store
export default new Vuex.Store({
  modules: {
    gameContent,
    videoContent,
    studyContent,
    appContent,
    bookContent,
    netdiskContent,
    scriptContent
  },
});
